"use client";

import { useEffect, useState, useCallback } from "react";
import api from "@/lib/axios";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";

interface Product {
    id: number;
    network: string;
    name: string;
    price: number;
}

export default function AdminProductsPage() {
    const [products, setProducts] = useState<Product[]>([]);
    const [isOpen, setIsOpen] = useState(false);
    const [newProduct, setNewProduct] = useState({ network: '', name: '', price: '', data_amount: '' });

    const fetchProducts = useCallback(async () => {
        const res = await api.get('/products'); // Use public or admin endpoint
        setProducts(res.data);
    }, []);

    useEffect(() => {
        Promise.resolve().then(() => fetchProducts());
    }, [fetchProducts]);

    const handleCreate = async () => {
        try {
            await api.post('/admin/products', newProduct);
            setIsOpen(false);
            fetchProducts();
        } catch (e) {
            alert("Error creating product");
        }
    };

    const handleDelete = async (id: number) => {
        if (confirm("Delete this bundle?")) {
            await api.delete(`/admin/products/${id}`);
            fetchProducts();
        }
    };

    return (
        <div className="space-y-6">
            <div className="flex justify-between">
                <h2 className="text-2xl font-bold">Manage Products</h2>
                <Dialog open={isOpen} onOpenChange={setIsOpen}>
                    <DialogTrigger asChild>
                        <Button>Add Bundle</Button>
                    </DialogTrigger>
                    <DialogContent>
                        <DialogHeader>
                            <DialogTitle>Add New Bundle</DialogTitle>
                        </DialogHeader>
                        <div className="space-y-4 py-4">
                            <Input placeholder="Network (e.g. MTN)" value={newProduct.network} onChange={e => setNewProduct({ ...newProduct, network: e.target.value })} />
                            <Input placeholder="Name (e.g. 1GB)" value={newProduct.name} onChange={e => setNewProduct({ ...newProduct, name: e.target.value })} />
                            <Input placeholder="Price" type="number" value={newProduct.price} onChange={e => setNewProduct({ ...newProduct, price: e.target.value })} />
                            <Input placeholder="Data Amount (e.g. 1024)" value={newProduct.data_amount} onChange={e => setNewProduct({ ...newProduct, data_amount: e.target.value })} />
                            <Button onClick={handleCreate} className="w-full">Create</Button>
                        </div>
                    </DialogContent>
                </Dialog>
            </div>

            <div className="bg-white rounded border">
                <table className="w-full text-sm text-left">
                    <thead className="bg-gray-50 uppercase text-gray-500">
                        <tr>
                            <th className="px-6 py-3">Network</th>
                            <th className="px-6 py-3">Name</th>
                            <th className="px-6 py-3">Price</th>
                            <th className="px-6 py-3">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-200">
                        {products.map((p) => (
                            <tr key={p.id}>
                                <td className="px-6 py-4">{p.network}</td>
                                <td className="px-6 py-4">{p.name}</td>
                                <td className="px-6 py-4">GHS {p.price}</td>
                                <td className="px-6 py-4">
                                    <Button size="sm" variant="destructive" onClick={() => handleDelete(p.id)}>Delete</Button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}
